* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.btn-wrap {
  justify-content: center;
  display: flex;
}

.btn {
  cursor: pointer;
  color: #fff;
  background: #ffb866;
  border: 0;
  border-radius: 2px;
  margin-top: 40vh;
  padding: 10px 20px;
  font-weight: bold;
  transition: all .2s ease-in;
  display: inline-block;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, .1);
}

.btn:hover {
  background: darken(#ffb866, 10%);
}

.btn:disabled {
  color: #666;
  background-color: #ccc;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, .1);
}

.btn:nth-child(2n) {
  margin-left: 10px;
}

.timer-wrapper {
  justify-content: center;
  margin-top: 20vh;
  display: flex;
}

.datetime-input {
  width: 460px;
  border: 0;
  padding: 18px;
  box-shadow: 0 6px 10px rgba(0, 0, 0, .1);
}

.datetime-btn {
  cursor: pointer;
  color: #fff;
  background: #ffb866;
  border: 0;
  border-radius: 2px;
  margin-left: 20px;
  padding: 12px 36px;
  font-size: 16px;
  font-weight: bold;
  transition: all .2s ease-in;
  display: inline-block;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, .1);
}

.datetime-btn:hover {
  background: darken(#ffb866, 10%);
}

.datetime-btn:disabled {
  color: #666;
  background-color: #ccc;
  border: 1px solid #999;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, .1);
}

.timer {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.field .value {
  margin-top: 20px;
  font-size: 65px;
  font-weight: 600;
}

span.label {
  margin-left: 5px;
  margin-right: 15px;
}

.form {
  justify-content: center;
  align-items: flex-end;
  margin-top: 33.3333vh;
  display: flex;
}

form input {
  width: 200px;
  border: 0;
  margin-top: 10px;
  margin-right: 40px;
  padding: 12px 18px;
  display: block;
  box-shadow: 0 6px 10px rgba(0, 0, 0, .1);
}

form label {
  font-weight: 700;
}

form button {
  width: 160px;
  cursor: pointer;
  color: #fff;
  background: #ffb866;
  border: 0;
  border-radius: 2px;
  padding: 10px 18px;
  font-weight: bold;
  transition: all .2s ease-in;
  display: inline-block;
  box-shadow: 0 6px 10px rgba(0, 0, 0, .1);
}

form button:hover {
  background: darken(#ffb866, 10%);
}

form button:disabled {
  color: #666;
  background-color: #ccc;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, .1);
}

